import * as React from "react";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";

export default function DenseTable(props) {
  return (
    <TableContainer
      style={{
        margin: "10px auto",
        width: "600px",
        height: "200px",
        background: "linear-gradient(159deg, #2d2d3a 0%, #2b2b35 100%)"
      }}
      component={Paper}
    >
      <Table stickyHeader={true} size="small" aria-label="a dense table">
        <TableHead>
          <TableRow>
            <TableCell
              style={{ background: "rgba(250, 250, 252, 0.75)" }}
              align="center"
            >
              Truck Number
            </TableCell>
            <TableCell
              style={{ background: "rgba(250, 250, 252, 0.75)" }}
              align="center"
            >
              Truck PayLoad
            </TableCell>
            <TableCell
              style={{ background: "rgba(250, 250, 252, 0.75)" }}
              align="center"
            >
              Bucket Weight
            </TableCell>
            <TableCell
              style={{ background: "rgba(250, 250, 252, 0.75)" }}
              align="center"
            >
              Pass count
            </TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {props.value.map((row, index) => (
            <TableRow
              style={{ color: "#fafafc" }}
              key={index}
              sx={{ "&:last-child td, &:last-child th": { border: 0 } }}
            >
              <TableCell
                style={{ color: "rgba(250, 250, 252, 0.75)" }}
                align="center"
              >
                {row.truckNum}
              </TableCell>
              <TableCell
                style={{ color: "rgba(250, 250, 252, 0.75)" }}
                align="center"
              >
                {row.truckPayload}
              </TableCell>
              <TableCell
                style={{ color: "rgba(250, 250, 252, 0.75)" }}
                align="center"
              >
                {row.bucketWeight}
              </TableCell>
              <TableCell
                style={{ color: "rgba(250, 250, 252, 0.75)" }}
                align="center"
              >
                {row.passCount}
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}
