import "./styles.css";
import { useState, useEffect } from "react";
import CircularProgressWithLabel from "./components/progress";
import { Box, Snackbar, Button, Alert } from "@mui/material";
import DenseTable from "./components/logTable";

export default function App() {
  const [simTime, setSimTime] = useState(500);
  const [payloadCapacity, setPayLoadCapacity] = useState(400);
  const [bucketCapacity, setBucketCapacity] = useState(100);
  const [maxBucketWeight, setMaxBucketWeight] = useState(40);
  const [openAlert, setOpenAlert] = useState(false);

  const [storeStatus, setStoreStatus] = useState("");
  const [alertStatus, setAlertStatus] = useState(true);
  const maxpass = 20;

  const [table, setTable] = useState([]);

  const [trucksLoaded, setTrucksLoaded] = useState(0);
  const [truckNum, setTruckNum] = useState(1);
  const [isTruckFull, setIsTruckFull] = useState(false);
  const [truckPayload, setTruckPayload] = useState(0);
  const [bucketWeight, setBucketWeight] = useState(0);
  const [passCount, setPassCount] = useState(0);
  const [store, setStore] = useState(0);
  const [overload, setOverload] = useState(0);

  const [intervalId, setIntervalId] = useState(0);

  const handleClose = () => {
    setOpenAlert(false);
  };

  //Function Handler to start and pause simulation
  const handleSimulate = () => {
    if (intervalId) {
      clearInterval(intervalId);
      setIntervalId(0);
      return;
    }
    const newIntervalId = setInterval(() => {
      setBucketWeight((prev) => prev + (1 + Math.floor(Math.random() * 10)));
    }, simTime);
    setIntervalId(newIntervalId);
  };

  //Increase Truck payload after bucketWeight exceeds maxBucketWeight
  const checkBucketWeight = () => {
    //console.log(bucketWeight, maxBucketWeight);
    if (!bucketWeight) return;

    if (bucketWeight + truckPayload === payloadCapacity) {
      console.log("yes");
      setIsTruckFull(true);
      setStore((prev) => prev + 1);
      return;
    }

    if (bucketWeight + truckPayload > payloadCapacity) {
      setOverload((prev) => prev + 1);
      return;
    }

    if (bucketWeight > maxBucketWeight) {
      setStore((prev) => prev + 1);
    }
  };

  const handleStore = () => {
    if (!store) return;

    // stopping the bucket
    clearInterval(intervalId);
    setIntervalId(0);

    if (!isTruckFull) {
      // store functionality: inc truck payload, inc passcount, set bucketwt = 0
      setTimeout(() => {
        setTruckPayload((prev) => prev + bucketWeight);
        setPassCount((prev) => prev + 1);
        setBucketWeight(0);

        // after storing, start collecting in the bucket
        const newIntervalId = setInterval(() => {
          setBucketWeight(
            (prev) => prev + (1 + Math.floor(Math.random() * 10))
          );
        }, simTime);
        setIntervalId(newIntervalId);
      }, simTime * 2);
    } else {
      setTruckPayload((prev) => prev + bucketWeight);
      setPassCount((prev) => prev + 1);
      setBucketWeight(0);

      setTimeout(() => {
        setTrucksLoaded((prev) => prev + 1);
      }, simTime);

      setIsTruckFull(false);
    }
  };

  const handleOverLoad = () => {
    if (!overload) return;

    setStoreStatus("error");
    setOpenAlert(true);
    setAlertStatus(false);
    setTimeout(() => {
      setStoreStatus("");
    }, simTime * 2);

    if (intervalId) {
      clearInterval(intervalId);
      setIntervalId(0);
    }
    setTimeout(() => {
      if (bucketWeight) {
        setBucketWeight((prev) => prev - 1);
      }
    }, simTime * 2);
  };

  const nextTruck = () => {
    if (!trucksLoaded) return;

    setTimeout(() => {
      console.log("next truck...");
      setTruckPayload(0);
      setTruckNum((prev) => prev + 1);

      // starts to load bucket for new truck
      const newIntervalId = setInterval(() => {
        setBucketWeight((prev) => prev + (1 + Math.floor(Math.random() * 10)));
      }, simTime);
      setIntervalId(newIntervalId);
    }, simTime);
  };

  useEffect(checkBucketWeight, [bucketWeight]);
  useEffect(handleStore, [store]);
  useEffect(handleOverLoad, [overload]);
  useEffect(nextTruck, [trucksLoaded]);

  useEffect(() => {
    if (passCount > 19) {
      clearInterval(intervalId);
      setIntervalId(0);
      setPassCount(0);
    }
    if (passCount) {
      setTable((prev) => {
        setStoreStatus("success");
        setOpenAlert(true);
        setAlertStatus(true);
        setTimeout(() => {
          setStoreStatus("");
        }, simTime * 2);
        let temp = {
          truckNum,
          truckPayload,
          bucketWeight: prev.length
            ? prev[prev.length - 1].truckNum === truckNum
              ? truckPayload - prev[prev.length - 1].truckPayload
              : truckPayload
            : truckPayload,
          passCount
        };
        return [...prev, temp];
      });
    }
  }, [passCount]);

  return (
    <div className="App">
      {/* <Box tytype="number" 
      <TextField label="Outlined" variant="outlined" /><TextField size="small"  label="Outlined" variant="outlined" /> */}
      {/* appleva iruntha maari this sandbox la mathu okkk na animation mudikaren okk*/}
      <Box
        sx={{
          margin: "0",
          width: "100%",
          height: "700px"
        }}
      >
        <h1 className="heading1">Store Feature Payload System</h1>
        <h4 className="heading4">Truck no: {truckNum}</h4>
        <div className="progress-container">
          <div className="progress-bar">
            <h5 className="heading5">Bucket weight:</h5>
            <h3>
              {bucketWeight} / {bucketCapacity} tons
            </h3>
            <CircularProgressWithLabel
              value={(bucketWeight * 100) / bucketCapacity}
            />
          </div>
          <div className="progress-bar">
            <h5 className="heading5">Truck payload:</h5>
            <h3>
              {truckPayload} / {payloadCapacity} tons
            </h3>
            <CircularProgressWithLabel
              value={(truckPayload * 100) / payloadCapacity}
            />
          </div>
          <div className="progress-bar">
            <h5 className="heading5">Pass count:</h5>
            <h3>
              {passCount} / {maxpass}
            </h3>
            <CircularProgressWithLabel value={(passCount * 100) / maxpass} />
          </div>
        </div>
        {/* <Typography variant="h5" fontFamily="'Roboto Condensed', sans-serif">
          Pass count: {passCount}
        </Typography> */}

        <Button
          className={storeStatus}
          style={{
            backgroundColor: "#ffc107",
            color: "#000",
            fontWeight: "bold",
            letterSpacing: "2.3px",
            margin: "20px",
            padding: "10px 20px"
          }}
          variant="contained"
          onClick={handleSimulate}
        >
          Store
        </Button>
        {/* To Create log details */}
        <DenseTable value={table} />
      </Box>
      <Snackbar
        open={openAlert}
        autoHideDuration={1500}
        onClose={handleClose}
        style={{ left: "50%", bottom: "8%", transform: "translate(-50%,0%)" }}
      >
        <Alert
          onClose={handleClose}
          severity={alertStatus ? "success" : "error"}
          sx={{ width: "100%" }}
        >
          {alertStatus ? "Stored" : "Truck Overload"}
        </Alert>
      </Snackbar>
    </div>
  );
}
